@import url(https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
html, body, #root {
  height: 100%;
  margin: 0;
  overflow: hidden;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  font-family: 'Work Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
}

body {
  color: var(--theme-text);
  background: var(--theme-background);
  transition: all .25s ease-in-out;

  --theme-background: #ffffff;
  --theme-footer-background: #333;
  --theme-surface: transparent;
  --theme-text: #151618;
  --theme-footer-text: #ffffff;
  --theme-primary: black;
  --theme-separator: #dfdfdf;
  --theme-footer-separator: #333;
  --theme-border: #ddd;
  --theme-surface-border: #ddd;

  --theme-active-opacity: 1;
  --theme-medium-opacity: 0.60;
  --theme-disabled-opacity: 0.38;

  --theme-drop-shadow: none;

  --menu-height: 67px;
  --footer-height: 60px;
}

body.dark-mode {
  --theme-background: #151618;
  --theme-footer-background: #151618;
  --theme-surface: #25282c;
  --theme-text: #ffffff;
  --theme-footer-text: #ffffff;
  --theme-primary: black;
  --theme-separator: rgba(244,245,246,0.12);
  --theme-footer-separator: rgba(244,245,246,0.12);
  --theme-border: #151618;
  --theme-surface-border: rgba(244,245,246,0.12);
  --theme-drop-shadow: drop-shadow(0 0 0.75rem var(--theme-primary));
}
.app {
  min-width: 100%;
  width: 100%;
  min-height: 100%;
  height: 100%;
  /* overflow: hidden; */
}

.app-content {
  width: 100%;
  height: calc(100% - var(--menu-height) - var(--footer-height));
  /* margin-top: var(--menu-height); */
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  overflow-y: auto;
  overflow-x: hidden;
}

@media(min-width: 992px) {
  .app-content {
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    overflow-y: hidden;
  }
}
.menu-container {
  /* position: fixed; */
  top: 0;
  width: 100%;
  padding: 0 32px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  box-sizing: border-box;
  z-index: 99;
  height: var(--menu-height);
  box-sizing: border-box;
  border-bottom: 1px solid var(--theme-separator);
}

@media screen and (max-width: 990px) {
  .menu-container{
    height: 0px;
  }
}

.menu-container .logo {
  display: block;
}
.menu-container .logo > img {
  width: 150px;
}

.menu-nav {
  color: black;
  display: none;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 0;
  padding: 0;
  height: 100%;
  list-style-type: none;
  list-style-position: inside;
  counter-reset: menuCounter;
}
.menu-nav > li {
  counter-increment: menuCounter;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  text-align: center;
  text-transform: capitalize;
  -webkit-text-decoration-style: solid;
          text-decoration-style: solid;
  max-width: 225px;
  opacity: var(--theme-disabled-opacity);
  cursor: pointer;
  box-sizing: border-box;
  transition: opacity .15s ease-in-out;
}
.menu-nav > li.selected {
  /* box-shadow: inset 0 -5px 0 -1px var(--theme-primary); */
  box-shadow: inset 0 -5px 0 -1px black;
  opacity: var(--theme-active-opacity);
}
.menu-nav > li:hover {
  opacity: var(--theme-active-opacity);
}
.menu-nav > li::before {
  content: counter(menuCounter) ".";
  padding-right: 4px;
  font-size: 20px;
  font-weight: 700;
}
.menu-nav > li > h2 {
  display: inline-block;
  margin: 0;
  padding: 24px 0;
  font-weight: 700;
  font-size: 20px;
}

.mode-icon {
  min-width: 24px;
  min-height: 24px;
  cursor: pointer;
}

@media(min-width: 992px) {
  .menu-container {
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
  }
  .menu-nav {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
  }
}

.arrow-left {
  position: absolute;
  margin-right: 15%;
  margin-top: 22px;
}

.arrow-right {
  position: absolute;
  margin-right: -15%;
  margin-top: 22px;
}
.footer {
  display: none;
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 99;
  height: var(--footer-height);
  box-sizing: border-box;
  /* display: flex; */
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  font-size: 18px;
  font-weight: 400;
  padding: 0 32px;
  border-top: 1px solid var(--theme-footer-separator);
  color: var(--theme-footer-text);
  /* background: var(--theme-footer-background); */
  background-color: white;
}

.footer > * {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.footer > *:first-child {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
.footer > *:nth-child(2) {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0 12px;
}
.footer > *:last-child {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.footer button {
  text-align: right;
  cursor: pointer;
  background: var(--theme-primary);
  color: white;
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
  line-height: 24px;
  outline: none;
  border: 2px solid transparent;
  border-radius: 25px;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  max-width: 75px;
  padding: 6px;
  box-sizing: border-box;
  transition: all .2s ease;
}
.footer button:focus, 
.footer button:active {
  box-shadow: inset 0 0 0 2px white;
}
.footer button:disabled {
  cursor: auto;
  opacity: 0.38;
}

.footer button > span {
  display: none;
}
.footer button > svg {
  min-width: 24px;
  min-height: 24px;
}

@media screen and (max-width: 990px) {
  .footer {
    display: -webkit-flex;
    display: flex;
    font-size: 24px;
  }
  .footer button {
    width: 100%;
    max-width: 300px;
  }
  .footer button > span {
    display: inline-block;
  }
  .footer button > svg {
    display: none;
  }
}
.settings {
  padding: 32px;
  box-sizing: border-box;
  text-align: center;
}

@media screen and (max-width: 990px) {
  .settings {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }
  
}

.settings-group {
  max-width: 450px;
  margin: 0 auto;
  margin-bottom: 32px;
}
.settings-group > h3 {
  text-transform: capitalize;
  font-weight: 500;
  font-size: 20px;
  margin-top: 0;
  margin-bottom: 16px;
}

.settings-group-disclaimer {
  font-size: 14px;
  line-height: 24px;
  opacity: var(--theme-medium-opacity);
}

.settings-options {
  margin: 0;
  padding: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.settings-options .settings-options-text {
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
}

.settings-group-label {
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.settings-group-label > span:first-child {
  padding-right: 12px;
}
.settings-group-label > span:last-child {
  padding-left: 12px;
}
.settings-group-label > .price {
  font-size: 14px;
  text-transform: capitalize;
}

.settings-group-benefits > p {
  font-weight: 500;
}
.settings-group-benefits > ul {
  font-size: 16px;
  line-height: 24px;
  text-align: left;
}
.settings-group-benefits > ul > li {
  margin-bottom: .6em;
}

@media(min-width: 992px) {
  .settings {
    -webkit-flex: 1 1;
            flex: 1 1;
    overflow-y: auto;
    min-width: 435px;
    padding: 48px;
    text-align: left;
  }
  .settings-group-label {
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  .settings-options {
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
  }
  .settings-options .settings-options-text {
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }
}
.option {
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}
.option > .price {
  font-size: 14px;
  padding-left: 12px;
}

.text-option {
  background: var(--theme-surface);
  border: 1px solid var(--theme-surface-border);
  padding: 12px 20px;
  margin-bottom: 12px;
  border-radius: 25px;
  width: 100%;
  box-sizing: border-box;
  text-transform: capitalize;
  transition: all .25s ease-in-out;
}
.text-option > span:first-child {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.text-option.active {
  border-color: var(--theme-primary);
  box-shadow: 0 0 0 2px var(--theme-primary);
  font-weight: 500;
}

.image-option {
  width: 75px;
  height: 75px;
  padding: 8px;
  margin-right: 15px;
  margin-bottom: 15px;
  margin-top: 10px;
  border-radius: 12px;
  background: var(--theme-surface);;
  border: 2px solid var(--theme-surface-border);
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.image-option.active {
  border: 2px solid var(--theme-primary);
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.image-option > img {
  max-width: 100%;
  max-height: 100%;
}

.color-option {
  width: 48px;
  height: 48px;
